<template>
	<div id="app">
		<!--头部-->
		<header class="header mui-bar mui-bar-nav">
			<a class="mui-action-back iconfont mui-pull-left">&#xe638;</a>
			<h1 class="mui-title">标题</h1>
			<a class="right mui-icon-right-nav mui-pull-right">保存</a>
		</header>
		<!--底部-->
		<nav class="mui-bar mui-bar-tab footer">
			<a id="goHome" class="mui-tab-item" href="javascript:void(0)">
				<span class="mui-icon iconfont">&#xe60a;</span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a id="chart" class="mui-tab-item" href="javascript:void(0)">
				<span class="mui-icon iconfont">&#xe608;</span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a id="personCenter" class="mui-tab-item" href="javascript:void(0)">
				<span class="mui-icon iconfont">&#xe609;</span>
				<span class="mui-tab-label">个人中心</span>
			</a>
		</nav>
		<!--内容-->
		<div id="muiContent" class="mui-content">
			{{msg}}
			<button id="btn">确定</button>
		</div>
	</div>
</template>

<script>
	import 'common/css/mui.css';
	import mui from 'common/js/mui.js'
	import 'common/js/zepto_1.1.3.js';
	mui.ready(function(){
		mui(document).on('tap','#btn',function(){
			mui.toast(this.innerHTML)
		});
	});
	
	export default {
		name: 'app',
		data() {
			return {
				msg: "haha"
			}
		},
		methods: {

		}
	}
</script>

<style>
	#app {
		font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #333;
	}
</style>


